'use client';

/**
 * LandingPage — Main landing experience
 * Orchestrates gate → grid reveal sequence
 */

import { useState } from 'react';
import { getFeaturedNodes, getNodeById, getConnectedNodes } from '@/lib/nodes';
import NeonGridCanvas from './NeonGridCanvas';
import EnterTheHive from './EnterTheHive';
import TheGrid from './TheGrid';
import NodeDrawer from './NodeDrawer';
import Link from 'next/link';
import { colors } from '@/lib/tokens';

export default function LandingPage() {
  const [hasEntered, setHasEntered] = useState(false);
  const [selectedNodeId, setSelectedNodeId] = useState<string | undefined>();

  const featuredNodes = getFeaturedNodes(6);
  const selectedNode = selectedNodeId ? getNodeById(selectedNodeId) : null;
  const connectedNodes = selectedNodeId ? getConnectedNodes(selectedNodeId) : [];

  if (!hasEntered) {
    return (
      <>
        <NeonGridCanvas particleCount={100} enableMotion={true} />
        <EnterTheHive onComplete={() => setHasEntered(true)} />
      </>
    );
  }

  return (
    <div className="relative w-full h-screen overflow-hidden">
      {/* Background */}
      <NeonGridCanvas particleCount={150} enableMotion={true} />

      {/* Main Grid */}
      <div className="relative z-10 w-full h-full">
        <TheGrid
          nodes={featuredNodes}
          selectedNodeId={selectedNodeId}
          onNodeClick={(node) => setSelectedNodeId(node.id)}
        />
      </div>

      {/* Header */}
      <header
        className="absolute top-0 left-0 right-0 z-20 px-12 py-8
                   border-b backdrop-blur-sm"
        style={{
          borderColor: `${colors.slateLight}20`,
          backgroundColor: `${colors.deepSpace}80`,
        }}
      >
        <div className="max-w-7xl mx-auto flex items-center justify-between">
          <h1
            className="text-2xl font-display font-bold px-4 py-2"
            style={{
              background: `linear-gradient(135deg, ${colors.aurumGold} 0%, ${colors.violetCore} 100%)`,
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              backgroundClip: 'text',
            }}
          >
            HoneyDrunk
          </h1>

          <nav className="flex gap-8 text-sm font-display">
            <Link
              href="/nodes"
              className="hover:text-electric-blue transition-colors px-4 py-2"
              style={{ color: colors.slateLight }}
            >
              The Grid
            </Link>
            <Link
              href="/signal"
              className="hover:text-electric-blue transition-colors px-4 py-2"
              style={{ color: colors.slateLight }}
            >
              Signal
            </Link>
            <Link
              href="/brand"
              className="hover:text-electric-blue transition-colors px-4 py-2"
              style={{ color: colors.slateLight }}
            >
              Brand
            </Link>
            <Link
              href="/about"
              className="hover:text-electric-blue transition-colors px-4 py-2"
              style={{ color: colors.slateLight }}
            >
              About
            </Link>
          </nav>
        </div>
      </header>

      {/* Featured content overlay */}
      <div
        className="absolute bottom-8 left-8 right-8 z-20 max-w-2xl
                   px-12 py-10 rounded-lg backdrop-blur-sm border"
        style={{
          backgroundColor: `${colors.deepSpace}90`,
          borderColor: `${colors.slateLight}30`,
        }}
      >
        <h2
          className="text-2xl font-display font-bold mb-5"
          style={{ color: colors.electricBlue }}
        >
          Build-in-Public • Zero-Bloat • Agents Amplify
        </h2>
        <p
          className="text-base mb-6 leading-relaxed"
          style={{ color: colors.offWhite }}
        >
          Welcome to The Hive. A living grid of interconnected nodes representing
          projects, systems, and experiments. Every node earns its keep. Every line
          of code tells a story.
        </p>
        <div className="flex gap-6">
          <Link
            href="/nodes"
            className="px-10 py-5 rounded-lg font-display font-semibold
                     transition-all duration-200 hover:scale-105"
            style={{
              backgroundColor: `${colors.violetCore}20`,
              borderWidth: '2px',
              borderColor: colors.violetCore,
              color: colors.offWhite,
            }}
          >
            Explore All Nodes
          </Link>
          <Link
            href="/about"
            className="px-10 py-5 rounded-lg font-display font-semibold
                     transition-all duration-200 hover:scale-105"
            style={{
              backgroundColor: `${colors.gunmetal}60`,
              borderWidth: '1px',
              borderColor: colors.slateLight,
              color: colors.slateLight,
            }}
          >
            Learn More
          </Link>
        </div>
      </div>

      {/* Node Detail Drawer */}
      <NodeDrawer
        node={selectedNode || null}
        onClose={() => setSelectedNodeId(undefined)}
        connectedNodes={connectedNodes}
      />
    </div>
  );
}
