'use client';

/**
 * EnterTheHive — Cinematic gate sequence
 * Boot animation that reveals The Grid
 */

import { useState, useEffect } from 'react';
import { colors } from '@/lib/tokens';

interface EnterTheHiveProps {
  onComplete: () => void;
}

const BOOT_MESSAGES = [
  'INITIALIZING HIVE PROTOCOL...',
  'LOADING NODE REGISTRY...',
  'ESTABLISHING NEURAL LINKS...',
  'CALIBRATING SIGNAL PROCESSORS...',
  'BOOTING GRID INTERFACE...',
  'READY.',
];

export default function EnterTheHive({ onComplete }: EnterTheHiveProps) {
  const [isBooting, setIsBooting] = useState(false);
  const [bootProgress, setBootProgress] = useState(0);
  const [currentMessage, setCurrentMessage] = useState(0);
  const [isComplete, setIsComplete] = useState(false);

  useEffect(() => {
    if (!isBooting) return;

    // Progress animation
    const progressInterval = setInterval(() => {
      setBootProgress((prev) => {
        if (prev >= 100) {
          clearInterval(progressInterval);
          return 100;
        }
        return prev + 2;
      });
    }, 24);

    // Message rotation
    const messageInterval = setInterval(() => {
      setCurrentMessage((prev) => {
        if (prev >= BOOT_MESSAGES.length - 1) {
          clearInterval(messageInterval);
          return prev;
        }
        return prev + 1;
      });
    }, 200);

    return () => {
      clearInterval(progressInterval);
      clearInterval(messageInterval);
    };
  }, [isBooting]);

  useEffect(() => {
    if (bootProgress >= 100) {
      setTimeout(() => {
        setIsComplete(true);
        setTimeout(() => {
          onComplete();
        }, 600);
      }, 300);
    }
  }, [bootProgress, onComplete]);

  const handleEnter = () => {
    setIsBooting(true);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' || e.key === ' ') {
      handleEnter();
    }
  };

  if (isComplete) {
    return (
      <div
        className="fixed inset-0 z-50 flex items-center justify-center
                   bg-deep-space transition-opacity duration-600"
        style={{
          opacity: 0,
          animation: 'fadeOut 600ms ease-out forwards',
        }}
      />
    );
  }

  if (isBooting) {
    return (
      <div
        className="fixed inset-0 z-50 flex items-center justify-center
                   bg-deep-space"
      >
        <div className="max-w-2xl w-full px-8">
          {/* Boot messages */}
          <div className="mb-8 space-y-2 font-mono text-sm">
            {BOOT_MESSAGES.map((message, index) => (
              <div
                key={message}
                className="transition-opacity duration-200"
                style={{
                  opacity: index <= currentMessage ? 1 : 0.3,
                  color: index === currentMessage ? colors.electricBlue : colors.slateLight,
                }}
              >
                {index < currentMessage && '✓ '}
                {index === currentMessage && '> '}
                {message}
              </div>
            ))}
          </div>

          {/* Progress bar */}
          <div className="relative h-2 bg-gunmetal rounded-full overflow-hidden">
            <div
              className="absolute inset-y-0 left-0 rounded-full transition-all duration-100"
              style={{
                width: `${bootProgress}%`,
                background: `linear-gradient(90deg, ${colors.violetCore} 0%, ${colors.electricBlue} 100%)`,
                boxShadow: `0 0 20px ${colors.violetCore}80`,
              }}
            />
          </div>

          {/* Progress percentage */}
          <div
            className="mt-4 text-center font-mono text-lg"
            style={{ color: colors.electricBlue }}
          >
            {bootProgress}%
          </div>
        </div>
      </div>
    );
  }

  return (
    <div
      className="fixed inset-0 z-40 flex flex-col items-center justify-center
                 bg-gradient-to-b from-deep-space via-gunmetal to-deep-space"
    >
      {/* Tagline */}
      <div className="text-center mb-12 space-y-6">
        <h1
          className="text-6xl md:text-7xl font-display font-bold tracking-tight"
          style={{
            background: `linear-gradient(135deg, ${colors.aurumGold} 0%, ${colors.violetCore} 100%)`,
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            backgroundClip: 'text',
          }}
        >
          The HoneyDrunk Grid
        </h1>
        <p
          className="text-xl md:text-2xl font-display font-semibold"
          style={{ color: colors.electricBlue }}
        >
          Structure meets soul. Code meets art.
        </p>
      </div>

      {/* Enter button */}
      <button
        onClick={handleEnter}
        onKeyDown={handleKeyPress}
        className="group relative px-12 py-6 rounded-lg font-display font-semibold text-lg
                   transition-all duration-200 hover:scale-105 active:scale-95
                   focus:outline-none focus:ring-2 focus:ring-offset-2"
        style={{
          backgroundColor: `${colors.violetCore}20`,
          borderWidth: '2px',
          borderColor: colors.violetCore,
          color: colors.offWhite,
          boxShadow: `0 0 40px ${colors.violetCore}60`,
        }}
      >
        {/* Animated glow */}
        <div
          className="absolute inset-0 rounded-lg opacity-0 group-hover:opacity-100
                     transition-opacity duration-200 -z-10 blur-xl"
          style={{
            backgroundColor: colors.violetCore,
          }}
        />

        <span className="relative z-10">Enter the Hive</span>

        {/* Pulse ring */}
        <div
          className="absolute inset-0 rounded-lg animate-ping opacity-20"
          style={{
            borderWidth: '2px',
            borderColor: colors.violetCore,
          }}
        />
      </button>

      {/* Footer line */}
      <div
        className="absolute bottom-12 text-center font-display text-sm tracking-wider uppercase"
        style={{ color: colors.slateLight }}
      >
        Boot. Build. Refactor. Evolve.
      </div>
    </div>
  );
}
